* {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
}

body, p, h2, a {
  font-size: unset;
  -webkit-text-decoration: unset;
  text-decoration: unset;
  color: unset;
  margin: 0;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  display: flex;
}

.card__content {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__image {
  width: 160px;
